import { useState, useEffect } from "react"
import './caluculation1.css'
import { FormInput } from "./FormInput"
import { animateScroll as scroll } from 'react-scroll'

const Caluculation1 = (props) => {
    const { info, title } = props//<=propsとして結果を持ってきている
    const [realEstate, setRealEstate] = useState("")
    const [wifeSpecial, setWifeSpecial] = useState(0)
    const [otherKojo, setOtherKojo] = useState(0)
    const [sKojo, setSKojo] = useState(0)
    const [jKojo, setJKojo] = useState(0)
    const [kazeiIncomeS1, setKazeiIncomeS1] = useState(0)
    const [kazeiIncomeS2, setKazeiIncomeS2] = useState(0)
    const [kazeiIncomeJ1, setKazeiIncomeJ1] = useState(0)
    const [kazeiIncomeJ2, setKazeiIncomeJ2] = useState(0)
    const [sTax1, setSTax1] = useState(0)
    const [sTax2, setSTax2] = useState(0)
    const [jTax1, setJTax1] = useState(0)
    const [jTax2, setJTax2] = useState(0)
    const [soneki, setSoneki] = useState(0)

    useEffect(()=>{
        scroll.scrollToTop()
    },[])

    /*控除内容が変更されたときに控除額を出しなおす */
    useEffect(() => {
        setSKojo(Number(info[2].shakaiHoken) + Number(info[2].seimeiHoken) + Number(info[2].jisinKojo) +
            Number(info[2].wifeKojo) + Number(wifeSpecial) + Number(info[2].huyoKojoSum1) + Number(info[2].kisoKojo) + Number(otherKojo))
        setJKojo(Number(info[3].shakaiHoken) + Number(info[3].seimeiHokenJ) + Number(info[3].jisinKojoJ) +
            Number(info[3].wifeKojoJ) + Number(wifeSpecial) + Number(info[3].kisoKojoJ) + Number(otherKojo))
    }, [wifeSpecial, otherKojo, info])

    /*控除額に変更があった場合に課税所得を出しなおす */
    useEffect(() => {
        setKazeiIncomeS1(info[0].kyuyoIncome - sKojo)
        setKazeiIncomeJ1(info[0].kyuyoIncome - jKojo)
    }, [sKojo, jKojo, info[0].kyuyoIncome])

    useEffect(() => {
        setKazeiIncomeS2(soneki - sKojo)
        setKazeiIncomeJ2(soneki - jKojo)
    }, [sKojo, jKojo, soneki])

    /*申告前の税の設定 */
    useEffect(() => {
        /*所得税 */
        if (kazeiIncomeS1 < 195) {
            setSTax1(Math.round((kazeiIncomeS1 * 0.05) * 1.021 * 10) / 10)
        } else if (kazeiIncomeS1 >= 195 && kazeiIncomeS1 < 330) {
            setSTax1(Math.round((kazeiIncomeS1 * 0.1 - 9.75) * 1.021 * 10) / 10)
        } else if (kazeiIncomeS1 >= 330 && kazeiIncomeS1 < 695) {
            setSTax1(Math.round((kazeiIncomeS1 * 0.2 - 42.75) * 1.021 * 10) / 10)
        } else if (kazeiIncomeS1 >= 695 && kazeiIncomeS1 < 900) {
            setSTax1(Math.round((kazeiIncomeS1 * 0.23 - 63.6) * 1.021 * 10) / 10)
        } else if (kazeiIncomeS1 >= 900 && kazeiIncomeS1 < 1800) {
            setSTax1(Math.round((kazeiIncomeS1 * 0.33 - 153.6) * 1.021 * 10) / 10)
        } else if (kazeiIncomeS1 >= 1800 && kazeiIncomeS1 < 4000) {
            setSTax1(Math.round((kazeiIncomeS1 * 0.4 - 279.6) * 1.021 * 10) / 10)
        } else {
            setSTax1(Math.round((kazeiIncomeS1 * 0.45 - 479.6) * 1.021 * 10) / 10)
        }

        /*住民税 */
        setJTax1(Math.round(kazeiIncomeJ1 * 0.1 * 10) / 10)
    }, [kazeiIncomeS1, kazeiIncomeJ1])

    /*申告後の税の設定 */
    useEffect(() => {
        /*所得税 */
        if (kazeiIncomeS2 < 195) {
            setSTax2(Math.round((kazeiIncomeS2 * 0.05) * 1.021 * 10) / 10)
        } else if (kazeiIncomeS2 >= 195 && kazeiIncomeS2 < 330) {
            setSTax2(Math.round((kazeiIncomeS2 * 0.1 - 9.75) * 1.021 * 10) / 10)
        } else if (kazeiIncomeS2 >= 330 && kazeiIncomeS2 < 695) {
            setSTax2(Math.round((kazeiIncomeS2 * 0.2 - 42.75) * 1.021 * 10) / 10)
        } else if (kazeiIncomeS2 >= 695 && kazeiIncomeS2 < 900) {
            setSTax2(Math.round((kazeiIncomeS2 * 0.23 - 63.6) * 1.021 * 10) / 10)
        } else if (kazeiIncomeS2 >= 900 && kazeiIncomeS2 < 1800) {
            setSTax2(Math.round((kazeiIncomeS2 * 0.33 - 153.6) * 1.021 * 10) / 10)
        } else if (kazeiIncomeS2 >= 1800 && kazeiIncomeS2 < 4000) {
            setSTax2(Math.round((kazeiIncomeS2 * 0.4 - 279.6) * 1.021 * 10) / 10)
        } else {
            setSTax2(Math.round((kazeiIncomeS2 * 0.45 - 479.6) * 1.021 * 10) / 10)
        }

        /*住民税 */
        setJTax2(Math.round(kazeiIncomeJ2 * 0.1 * 10) / 10)
    }, [kazeiIncomeS2, kazeiIncomeJ2])


    /*不動産所得が入力されたときに損益通算を出す */
    useEffect(() => {
        setSoneki(Number(info[0].kyuyoIncome) + Number(realEstate))
    }, [realEstate])
    return (
        <>
            <br/>
            <br/>
            <br/>
            <br/>
            <br/>
            <br/>
            <br/>
            <br/>
            <br/>
            <br/>
            <br/>
            <br/>
            <br/>
            <br/>
            <h1 id="top">{title}</h1>
            <label>給与収入 : {info[0].taxIncome}</label>
            <br />
            <label><span>①</span> 給与所得 : {info[0].kyuyoIncome}</label>
            <br />
            <label><span>A</span> 損益通算 : {Number(info[0].kyuyoIncome) + Number(realEstate)}</label>
            <br />

            <label className="t-label"><span>②</span> 所得税控除</label>
            <table className="t">
                <tr>
                    <th>社会保険控除</th>
                    <td>{info[2].shakaiHoken}</td>
                </tr>
                <tr>
                    <th>生命保険控除</th>
                    <td>{info[2].seimeiHoken}</td>
                </tr>
                <tr>
                    <th>地震保険控除</th>
                    <td>{info[2].jisinKojo}</td>
                </tr>
                <tr>
                    <th>配偶者控除</th>
                    <td>{info[2].wifeKojo}</td>
                </tr>
                <tr>
                    <th>配偶者特別控除</th>
                    <td>{wifeSpecial}</td>
                </tr>
                <tr>
                    <th>扶養控除</th>
                    <td>{info[2].huyoKojoSum1}</td>
                </tr>
                <tr>
                    <th>基礎控除</th>
                    <td>{info[2].kisoKojo}</td>
                </tr>
                <tr>
                    <th>その他控除</th>
                    <td>{otherKojo}</td>
                </tr>
                <tr>
                    <th>計</th>
                    <td>{sKojo}</td>
                </tr>
            </table>

            <label className="t-label"><span>③</span> 住民税控除</label>

            <table className="t">
                <tr>
                    <th>社会保険控除</th>
                    <td>{info[3].shakaiHoken}</td>
                </tr>
                <tr>
                    <th>生命保険控除</th>
                    <td>{info[3].seimeiHokenJ}</td>
                </tr>
                <tr>
                    <th>地震保険控除</th>
                    <td>{info[3].jisinKojoJ}</td>

                </tr>
                <tr>
                    <th>配偶者控除</th>
                    <td>{info[3].wifeKojoJ}</td>
                </tr>
                <tr>
                    <th>配偶者特別控除</th>
                    <td>{wifeSpecial}</td>
                </tr>
                <tr>
                    <th>扶養控除</th>
                    <td>なし</td>
                </tr>
                <tr>
                    <th>基礎控除</th>
                    <td>{info[3].kisoKojoJ}</td>
                </tr>
                <tr>
                    <th>その他控除</th>
                    <td>{otherKojo}</td>
                </tr>
                <tr>
                    <th>計</th>
                    <td>{jKojo}</td>
                </tr>
            </table>
            <br />
            <p>★ 申告前の税額</p>
            <p><span>①</span> - <span>②</span> ⇒ {kazeiIncomeS1}</p>
            <label>所得税 ⇒ {sTax1}</label>
            <br />
            <label><span>①</span> - <span>③</span> ⇒ {kazeiIncomeJ1}</label>
            <label>住民税 ⇒ {jTax1}</label>
            <br />
            <p><span>④</span> 計 : {sTax1 + jTax1}</p>
            <br />
            <p>★ 申告後の税額</p>
            <br />
            <label><span>A</span> - <span>②</span> ⇒ {kazeiIncomeS2}</label>
            <label>所得税 ⇒ {sTax2}</label>
            <br />
            <label><span>A</span> - <span>③</span> ⇒ {kazeiIncomeJ2}</label>
            <label>住民税 ⇒ {jTax2}</label>
            <br />
            <p><span>⑤</span>計 : {sTax2 + jTax2}</p>


            <h2>減税額 <span>④</span> - <span>⑤</span></h2>
            <lavel>所得税還付金</lavel>
            <p>{Number(sTax1) - Number(sTax2)}</p>
            <br />
            <lavel>住民税減税額</lavel>
            <p>{Number(jTax1) - Number(jTax2)}</p>
            <br />
            <label>年間節約税額</label>
            <p>{Math.floor(Number(sTax1) - Number(sTax2) + Number(jTax1) - Number(jTax2))}</p>
        </>
    )
}
export default Caluculation1
