import { useState, memo } from "react"
import './Select_module.css'

const Income = memo((props) => {
    const { number, type, charge, count, type2, deleteIncomeInput } = props
    const [open, setOpen] = useState(true)


    const deleteThisIncome = () => {
        setOpen(false)
        deleteIncomeInput(number)
    }

    let typeLabel1;
    if (type === "retire") {
        typeLabel1 = "退職金（万円）"
    } else if (type === "other") {
        typeLabel1 = "その他収入（万円）"
    } else if (type === "yachin") {
        typeLabel1 = "家賃収入（万円）"
    } else if (type === "nenkin") {
        typeLabel1 = "個人年金（万円）"
    } else {
        typeLabel1 = "その他（万円）"
    }

    let typeLabel2;
    if (type2 === "every") {
        typeLabel2 = "毎年"
    } else if (type2 === "before") {
        typeLabel2 = "まで毎年"
    } else if (type2 === "after") {
        typeLabel2 = "から毎年"
    } else {
        typeLabel2 = "の時に1回限り"
    }

    return (
        <>
            {open ?
                <div className="option-wrapper">
                    <br />
                    <label>種類 : {typeLabel1}</label>
                    <br />
                    <label>金額（年額） : {charge}万円</label>
                    <br />
                    {type2 === "every" ?
                        <label>回数 : {typeLabel2}</label> :
                        <label>回数 : {count}歳{typeLabel2}</label>
                    }
                    <button className="btn btn-border" onClick={deleteThisIncome}>削除</button>
                </div>
                : null}

        </>
    )
})

export default Income