import { useState, memo } from "react"
import './Select_module.css'


const Huyo = memo((props) => {
    const { number, deleteHuyoInput ,type,childAge,numberOfHuyo,self} = props
    const [open, setOpen] = useState(true)

    const deleteThisHuyo = () => {
        setOpen(false)
        deleteHuyoInput(number)
    }

    let typeLabel;
    if (type === "child") {
        typeLabel = " 扶養~22歳"
    } else if (type === "wife") {
        typeLabel = " 配偶者"
    } else if (type === "other") {
        typeLabel = " その他扶養"
    } else if (type === "sinior1") {
        typeLabel = " 老人同居"
    } else {
        typeLabel = " 老人別居"
    }
    return (
        <div>
            {open ?
                <div className="option-wrapper">
                        <>
                            <br />
                            <label>種類 :
                                {typeLabel}
                                <br />
                                <br />
                                {type === "child" ?
                                    <>
                                        <label>年齢 : {childAge}歳</label>
                                    </>
                                    :
                                    <>
                                        <label>人数 : {numberOfHuyo}人</label>
                                    </>
                                }
                                <br />
                            </label>

                            <label>対象 :
                                {self===0 ?
                                    " 本人"
                                    :
                                    " 配偶者"}

                                <br />
                            </label>
                            <button className="btn btn-border" onClick={deleteThisHuyo}>削除</button>
                        </>
                </div>
                :
                null}          
        </div>
    )
})

export default Huyo